<template>
  <div class="dict-main">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <el-button type="warning" size="small" @click="open()" icon="el-icon-plus">添加</el-button>
    </div>
    <el-form label-width="85px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :lg="6" :xl="4">
          <el-form-item label="字典类型：">
            <el-select v-model="dictType" placeholder="请选择类型">
              <el-option label="请选择" value=""></el-option>
              <el-option v-for="(item, index) in typeList"
                :key="index"
                :label="item.type"
                :value="item.type">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :lg="6" :xl="4">
          <el-button type="primary" icon="el-icon-search" @click="getTableData" size="small">{{$t('common.search') }}
          </el-button>
        </el-col>
      </el-row>
    </el-form>
    <el-table :data="dictInfo.data" v-loading="tableLoading">
      <el-table-column v-for="col in columns"
        :prop="col.prop"
        :key="col.prop"
        :label="col.label"
        :width="col.width">
      </el-table-column>
      <el-table-column
        label="操作"
        width="120">
        <template slot-scope="props">
          <el-dropdown trigger="click">
            <el-button type="text" size="mini">
              操作<i class="el-icon-arrow-down el-icon--right"></i>
            </el-button>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>
                <el-button type="text" size="mini" icon="el-icon-edit" @click="open(props.row)">修改</el-button>
              </el-dropdown-item>
              <el-dropdown-item>
                <el-button type="text" size="mini" icon="el-icon-plus" @click="open(props.row, 'add')">添加</el-button>
              </el-dropdown-item>
              <el-dropdown-item divided>
                <el-button type="text" size="mini" class="btn-text-red" icon="el-icon-delete" @click="handleDelete(props.row)">删除</el-button>
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="current"
      :page-size="pageSize"
      layout="total, prev, pager, next, jumper"
      background
      :total="dictInfo.total"
    ></el-pagination>
    <el-dialog :title="handleTitle" :visible.sync="handleVisible" width="40%" @close="close" center>
      <el-form :model="dictForm" :rules="dictFormRules" ref="dictForm" label-width="90px" size="small">
        <el-form-item label="标签名：" prop="name">
          <el-input type="text" v-model.trim="dictForm.name"></el-input>
        </el-form-item>
        <el-form-item label="数据值：" prop="value">
          <el-input type="text" v-model.trim="dictForm.value"></el-input>
        </el-form-item>
        <el-form-item label="类型：" prop="type">
          <el-input type="text" v-model.trim="dictForm.type"></el-input>
        </el-form-item>
        <el-form-item label="描述：" prop="description">
          <el-input type="text" v-model.trim="dictForm.description"></el-input>
        </el-form-item>
        <el-form-item label="排序：" prop="sort">
          <el-input type="text" v-model.trim="dictForm.sort"></el-input>
        </el-form-item>
        <el-form-item label="备注信息：" prop="remarks">
          <el-input type="text" v-model.trim="dictForm.remarks"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" type="primary" :loading="loading" @click="handleConfirm">确 定</el-button>
        <el-button size="small" @click="close">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import { mapState, mapActions } from 'vuex'
  import Api from '../../constants/Api'
  import {SUCCESS_CODE} from '../../constants/constant'
  export default {
    data() {
      return {
        handleVisible: false,
        handleTitle: '添加字典',
        dictForm: {},
        current: 1,
        pageSize: 10,
        dictType: '',
        columns: [
          {
            prop: 'id',
            label: '编号'
          },
          {
            prop: 'name',
            label: '标签名'
          },
          {
            prop: 'value',
            label: '数据值'
          },
          {
            prop: 'type',
            label: '类型'
          },
          {
            prop: 'description',
            label: '描述'
          }
        ],
        dictFormRules: {
          name: [{required: true, message: '请输入标签名', trigger: 'blur'}],
          value: [{required: true, message: '请输入数据值', trigger: 'blur'}],
          type: [{required: true, message: '请输入类型', trigger: 'blur'}],
          description: [{required: true, message: '请输入描述', trigger: 'blur'}]
        }
      }
    },
    computed: {
      ...mapState({
        dictInfo: state => state.dict.dictInfo,
        typeList: state => state.dict.typeList,
        loading: state => state.dict.loading,
        tableLoading: state => state.dict.tableLoading
      })
    },
    created() {
      this.getDictTypes()
      this.getTableData()
    },
    methods: {
      ...mapActions('dict', ['getDictData', 'tableAction', 'deleteDict', 'getDictTypes']),
      getTableData() {
        const params = {
          page: this.current,
          per_page: this.pageSize,
          type: this.dictType
        }
        this.getDictData(params)
      },
      open(props, type) {
        if (props && props.id && !type) {
          this.dictForm = {...props}
          this.handleTitle = '修改字典'
        } else if (props && props.id && type === 'add') {
          this.handleTitle = '添加字典'
          const {type, description} = props
          this.dictForm = {type, description}
          console.log(this.dictForm)
        } else {
          this.handleTitle = '添加字典'
          this.dictForm = {}
        }
        this.handleVisible = true
      },
      close() {
        this.$refs['dictForm'].resetFields()
        this.handleVisible = false
      },
      handleCurrentChange(val) {
        this.current = val
        this.getTableData()
      },
      handleDelete(props) {
        const vm = this
        vm.$confirm(`确认删除${props.name}？`, '确认', {
            confirmButtonText: vm.$t('common.ok'),
            cancelButtonText: vm.$t('common.cancel'),
            type: 'warning',
            center: true,
            beforeClose: async (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true
              const response = await this.deleteDict(props.id)
              console.log(response)
              if (response && response.code === SUCCESS_CODE) {
                instance.confirmButtonLoading = false
                vm.$message.success('删除成功')
                vm.getTableData()
              } else {
                instance.confirmButtonLoading = false
                vm.$message.error((response && response.data && response.data.message) || '删除失败')
              }
              done()
            } else {
              done()
            }
          }
          }).then(() => {}).catch(() => {})
      },
      handleConfirm() {
        const vm = this
        vm.$refs['dictForm'].validate(valid => {
          if (valid) {
            console.log(this.dictForm)
            if (!vm.dictForm.id) {
              vm.tableAction({
                url: Api.dictAdd,
                data: this.dictForm,
                callback: () => {
                  vm.$message.success('添加成功')
                  vm.close()
                  vm.current = 1
                  vm.getTableData()
                },
                callbackError: error => {
                  vm.$message.error((error && error.data && error.data.message) || '添加失败')
                }
              })
            } else {
              vm.tableAction({
                url: Api.dictEdit,
                data: this.dictForm,
                callback: () => {
                  vm.$message.success('修改成功')
                  vm.close()
                  vm.current = 1
                  vm.getTableData()
                },
                callbackError: error => {
                  vm.$message.error((error && error.data && error.data.message) || '修改失败')
                }
              })
            }
          }
        })
      }
    }
  }
</script>



